@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');


section{
    width: 100vw;
    background-color: #143259;
    display: flex;
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    flex-direction: column;
    color: #C1D4D9;
    font-size: 20px;
}

section .list--area{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: -10% 0 0px 0px;
    padding-bottom: 100px;
    z-index: 1;
}
section .anime--listview{
    margin-left: 30px;
    
}

section .anime--listview .list--itens{
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 300px;

}

section .list--area .anime--listview .list--itens .item--info{
    width: 200px;
    height: 300px;
    border-radius: 7px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    transform: scale(0.95);
    transition: all ease 0.2s;
}

section .list--area .anime--listview .list--itens .item--info:hover{
    transform: scale(1.5);
    z-index: 1;
    position: relative;
    margin-left: 30px;
    margin-right: 150px;
}

section .anime--listview .list--itens .item--info .anime--info{
    display: none;
    background-color: #143259;
    opacity: 0.8;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5), 8px 8px 10px 5px rgba(0, 0, 0, 0.56) ;
    border-radius: 0 7px 7px 0;
    font-size: 12px;
    color: #C1D4D9;
    padding-left: 5px;
}

section .anime--listview .list--itens .item--info:hover .anime--info{
    display: block;
    width: 300px;
}

section .anime--listview .list--itens .item--info .anime--info .title--info{
    font-size: 16px;
    font-weight: bold;
    margin: 5px 40px 15px 5px;
}

section .anime--listview .list--itens .item--info .anime--cover{
    width: 200px;
}

section .anime--listview .list--itens .item--info .anime--info .genres--info{
    font-size: 10px;
    color: #fff;
    font-weight: bold;
    margin: 5px 5px 5px 5px;
}

section .anime--listview .list--itens .item--info .anime--info .launch--info{
    font-size: 13px;
    margin: 5px 5px 5px 5px;
}
section .anime--listview .list--itens .item--info .anime--info .ratio--info{
    color: #00ff00;
    font-size: 25px;
    font-weight: bold;
    margin: 5px 5px 5px 5px;
}

section .anime--listview .list--itens .item--info .anime--info .control--buttons{
    width: 100%;
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 10px;

}

section .anime--listview .list--itens .item--info .anime--info .control--buttons .play-button{
    width: 80px;
    height: 25px;
    display: inherit;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 50px;    
    margin-left: 5px;
    border:1px solid #C1D4D9;
    background-color:#143259;
}

section .anime--listview .list--itens .item--info .anime--info .control--buttons .play-button:hover{
    opacity: 0.7;
}

section .anime--listview .list--itens .item--info .anime--info .control--buttons .play-button .play--icon{
    color: #C1D4D9;
}

@media only screen and (max-width: 600px) {
    section .list--area{
        overflow-x: hidden;
    }

    section .list--area .anime--listview .anime--gender{
        font-size: 7vw;
    }
 
    .featured--horizontal{
        padding-bottom: 0px;
    }
    .featured--info .featured--title{
        font-size: 6vw;
    }

    .featured--info .featured--description{
        font-size: 3.5vw;
        max-width: 55%;
    }
    .featured--info .featured--buttons .featured--watchbutton{
        width: 30vw;
    }

    section .list--area .anime--listview .list--itens .item--info:hover{
        transform: scale(1);
        margin-left: 1vw;
    }

    section .list--area .anime--listview .list--itens .item--info .anime--info .title--info{
        font-size: 6.5vw;
    }

    section .list--area .anime--listview .list--itens .item--info .anime--info .genres--info,
    section .list--area .anime--listview .list--itens .item--info .anime--info .launch--info{
        font-size: 4vw;
        font-weight: normal;
    }


}