*{
    margin: 0px;
    padding: 0px;
    vertical-align: baseline;
    list-style: none;
    border: 0;
}

body{
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #143259;
}

body::-webkit-scrollbar{
    width: 0.5vw;
    transition: all ease 0.2s;
}

body::-webkit-scrollbar-track{
    background: rgba(193, 212, 217, 0.1);
    border-radius: 12px;
}

body::-webkit-scrollbar-thumb{
    background-color: #C1D4D9;
    border-radius: 20px;
    border: 1px solid rgba(193, 212, 217, 0.7);
    opacity: 1;
}

