@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

.anime-page-background{
    width: 100vw;
    height: 100vh;
    background-color: rgba(20, 50, 89, 0.5);
    display: flex;
    position: fixed;
    justify-content: center;
    left: 0;
    top: 0;
    z-index: 4;   

}

.anime-page-background .anime-page-info{
    width: 45vw;
    height: 95vh;
    background-color: rgb(20, 50, 89);
    display: block;
    margin-top: 30px;
    position: relative;
    overflow-y: hidden;
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    border-radius: 25px;
    transition: all ease 0.2s;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5), 8px 8px 10px 5px rgba(0, 0, 0, 0.56);
}

.anime-page-background .anime-page-info .selected-anime-episode-info{
    color: #C1D4D9;
    margin-top: -6px;
    font-size: 17px;
    display: flex;
    flex-direction: column;
    background: linear-gradient(to bottom ,#000 , 35%, transparent 65%);
    border-bottom: 1px solid #666;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info{
    display: flex;
    padding-top: 30px;
    flex-direction: row;
    justify-content: end;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info .selected-anime-title{
    margin-right: 30px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info{
    display: flex;
    flex-direction: row;
    margin: 15px 30px;
    padding-bottom: 15px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .episode-title{
    width: 25vw;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons{
    margin: 0 30px;
    position: absolute;
    right: 0;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons .selected-season{
    width: 130px;
    height: 40px;
    background-color: #143259;
    border: 1px solid #666 ;
    color: #C1D4D9;
    text-align: center;
    border-radius: 10px;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info .ratio-info{
    color:#46d369;
    font-size: 18px;
    font-weight: bold;
    margin-right: 30px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info .launch-info{
    font-weight: bold;
    margin-right: 30px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title{
    margin-left: 30px;
    font-weight: bold;
    font-size: 1.2rem;
}
.anime-page-background .anime-page-info .selected-anime-episode-info .episode-number{
font-size: 20px;
font-weight: bold;
}

.anime-page-info video{
    border-radius: 25px 25px 0px 0;
}
.anime-page-info .close-button{
    width: 30px;
    height: 30px;
    display:flex;
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 1;
    border-radius: 30px;
    align-items: center;
    justify-content: center;
    margin: 30px 30px;
    background-color: #143259;
}

.anime-page-info .close-button .close-icon{
    font-size: 25px;
    color: #C1D4D9;
}

.anime-page-background .anime-page-info .episodes--list{
    height: 30vh;
    color: #e2f1f5;
    display: flex;
    overflow-y: scroll;
    font-size: 15px;
    scroll-behavior: smooth;
    flex-direction: column;
}

.anime-page-background .anime-page-info .episodes--list .episode div{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 10px 0;
    margin-left: 15px;
    transition: all ease 0.2s;
}

.anime-page-background .anime-page-info .episodes--list .episode:hover{
    font-size: 18px;
    cursor: pointer;

}

.anime-page-background .anime-page-info .episodes--list .episode div .episode-number{
    margin-right: 30px;
    font-weight: bold;
}

.anime-page-background .anime-page-info .episodes--list .episode div .episode-title{
    width: 30vw;
}

.anime-page-background .anime-page-info .episodes--list .episode div .episode-thumb{
    width: 150px;
    height: 90px;
    margin-right: 15px;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar{
    width: 12px;
    transition: all ease 0.2s;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar-track{
    background: rgba(193, 212, 217, 0.7);
    border-radius: 12px;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar-thumb{
    background-color: #143259;
    border-radius: 20px;
    border: 3px solid rgba(193, 212, 217, 0.7);
    opacity: 1;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar-thumb:hover{
    background-color: #143259;
    opacity: 0.5;
}

@media only screen and (max-width: 600px) {
    .anime-page-background{
        display: block;
        position:fixed;
       scroll-behavior: unset;
    }
    .anime-page-background .anime-page-info{
        width: inherit;
        height: inherit;
        margin-top: 0;
        border-radius: 0;
    }

    .anime-page-background .anime-page-info .close-button{
        width: 20px;
        height: 20px;
        margin: 15px 15px;
    }

    .anime-page-info video{
        border-radius: 0;
    }
    .anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .episode-title{
        width: 34vw;
        font-weight: normal;
        font-size: 5vw;
    }

    .anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons{
        margin-left: 0;
    }

    .anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons .selected-season{
        font-size: 4vw;
    }

    .anime-page-background .anime-page-info .episodes--list{
        height: 38vh;
    }
}
