@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

header{
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow-y: hidden;
    background-color: #143259;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    color: #fff;
}

header .home--navbar{
    width: 100vw;
    background: linear-gradient(to bottom, #0367ab 1%, transparent 99%);
    position: fixed;
    z-index: 2;
    height: 60px;
    padding: 10px 0 ;
}

header .home--navbar.black{
    background-color: #143259;
}
header .home--navbar .logo--site,
header .home--navbar .user--avatar{
    height: 50px;
}

header .home--navbar .user--avatar{
position: absolute;
right: 30px;
}

header .home--navbar .logo--site{
    position: absolute;
    left: 15px;
}

.featured--vertical{
    width: 100vw;
    height: 100%;
    position: absolute;
    top: 0;
    background: linear-gradient(to top, #143259 10%, transparent 90%);
}

.featured--horizontal{
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 180px;
}

.featured--title{
    font-family: 'Bebas Neue', cursive;
    font-size: 40px;
}
.featured--info{
    margin: -150px 0 5px 0;
    padding-left: 30px;
}

.featured--anime{
    width: 100vw;
    background-size: cover;
    background-position: center;
}
.featured--anime .mute-button{
    background-color: #143259;
    color:#fff;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    border: 1px solid #fff;
    border-radius: 25px;
    opacity: 0.4;
    bottom: 50%;
    right:30px;
    z-index: 1;
    transition: all ease 0.2s;
    transform: scale(1);
}

.featured--anime .mute-button:hover{
    opacity: 1;

    transform: scale(1.5);
}

.featured--watchbutton{
    display: inline-block;
    width: 150px;
    font-size: 20px;
    font-weight: bold;
    padding: 7px 20px;
    border-radius: 5px;
    text-decoration:none;
    text-align: center;
    margin: 30px 20px 0px 0px;
    opacity: 0.7;
    transition: all ease 0.2s;
    background-color: #C1D4D9;
    color: #143259;
}

.featured--watchbutton:hover{
    opacity: 0.4;
}
.featured--watchbutton .play--icon{
    padding: 0 10px 0px 10px;
}
.featured--description{
    max-width: 30%;
    font-size: 1.2vw;
}
  