header{
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    overflow-y: hidden;
    background-color: #143259;
    font-family: 'Poppins', sans-serif;
    font-size: 18px;
    color: #fff;
}

header .home--navbar{
    width: 100vw;
    background: linear-gradient(to bottom, #0367ab 1%, transparent 99%);
    position: fixed;
    z-index: 2;
    height: 60px;
    padding: 10px 0 ;
}

header .home--navbar.black{
    background-color: #143259;
}
header .home--navbar .logo--site,
header .home--navbar .user--avatar{
    height: 50px;
}

header .home--navbar .user--avatar{
position: absolute;
right: 30px;
}

header .home--navbar .logo--site{
    position: absolute;
    left: 15px;
}

.featured--vertical{
    width: 100vw;
    height: 100%;
    position: absolute;
    top: 0;
    background: linear-gradient(to top, #143259 10%, transparent 90%);
}

.featured--horizontal{
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 180px;
}

.featured--title{
    font-family: 'Bebas Neue', cursive;
    font-size: 40px;
}
.featured--info{
    margin: -150px 0 5px 0;
    padding-left: 30px;
}

.featured--anime{
    width: 100vw;
    background-size: cover;
    background-position: center;
}
.featured--anime .mute-button{
    background-color: #143259;
    color:#fff;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    border: 1px solid #fff;
    border-radius: 25px;
    opacity: 0.4;
    bottom: 50%;
    right:30px;
    z-index: 1;
    transition: all ease 0.2s;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.featured--anime .mute-button:hover{
    opacity: 1;

    -webkit-transform: scale(1.5);

            transform: scale(1.5);
}

.featured--watchbutton{
    display: inline-block;
    width: 150px;
    font-size: 20px;
    font-weight: bold;
    padding: 7px 20px;
    border-radius: 5px;
    text-decoration:none;
    text-align: center;
    margin: 30px 20px 0px 0px;
    opacity: 0.7;
    transition: all ease 0.2s;
    background-color: #C1D4D9;
    color: #143259;
}

.featured--watchbutton:hover{
    opacity: 0.4;
}
.featured--watchbutton .play--icon{
    padding: 0 10px 0px 10px;
}
.featured--description{
    max-width: 30%;
    font-size: 1.2vw;
}
  
section{
    width: 100vw;
    background-color: #143259;
    display: flex;
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    flex-direction: column;
    color: #C1D4D9;
    font-size: 20px;
}

section .list--area{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: -10% 0 0px 0px;
    padding-bottom: 100px;
    z-index: 1;
}
section .anime--listview{
    margin-left: 30px;
    
}

section .anime--listview .list--itens{
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 300px;

}

section .list--area .anime--listview .list--itens .item--info{
    width: 200px;
    height: 300px;
    border-radius: 7px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    transition: all ease 0.2s;
}

section .list--area .anime--listview .list--itens .item--info:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    z-index: 1;
    position: relative;
    margin-left: 30px;
    margin-right: 150px;
}

section .anime--listview .list--itens .item--info .anime--info{
    display: none;
    background-color: #143259;
    opacity: 0.8;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5), 8px 8px 10px 5px rgba(0, 0, 0, 0.56) ;
    border-radius: 0 7px 7px 0;
    font-size: 12px;
    color: #C1D4D9;
    padding-left: 5px;
}

section .anime--listview .list--itens .item--info:hover .anime--info{
    display: block;
    width: 300px;
}

section .anime--listview .list--itens .item--info .anime--info .title--info{
    font-size: 16px;
    font-weight: bold;
    margin: 5px 40px 15px 5px;
}

section .anime--listview .list--itens .item--info .anime--cover{
    width: 200px;
}

section .anime--listview .list--itens .item--info .anime--info .genres--info{
    font-size: 10px;
    color: #fff;
    font-weight: bold;
    margin: 5px 5px 5px 5px;
}

section .anime--listview .list--itens .item--info .anime--info .launch--info{
    font-size: 13px;
    margin: 5px 5px 5px 5px;
}
section .anime--listview .list--itens .item--info .anime--info .ratio--info{
    color: #00ff00;
    font-size: 25px;
    font-weight: bold;
    margin: 5px 5px 5px 5px;
}

section .anime--listview .list--itens .item--info .anime--info .control--buttons{
    width: 100%;
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 10px;

}

section .anime--listview .list--itens .item--info .anime--info .control--buttons .play-button{
    width: 80px;
    height: 25px;
    display: inherit;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 50px;    
    margin-left: 5px;
    border:1px solid #C1D4D9;
    background-color:#143259;
}

section .anime--listview .list--itens .item--info .anime--info .control--buttons .play-button:hover{
    opacity: 0.7;
}

section .anime--listview .list--itens .item--info .anime--info .control--buttons .play-button .play--icon{
    color: #C1D4D9;
}

@media only screen and (max-width: 600px) {
    section .list--area{
        overflow-x: hidden;
    }

    section .list--area .anime--listview .anime--gender{
        font-size: 7vw;
    }
 
    .featured--horizontal{
        padding-bottom: 0px;
    }
    .featured--info .featured--title{
        font-size: 6vw;
    }

    .featured--info .featured--description{
        font-size: 3.5vw;
        max-width: 55%;
    }
    .featured--info .featured--buttons .featured--watchbutton{
        width: 30vw;
    }

    section .list--area .anime--listview .list--itens .item--info:hover{
        -webkit-transform: scale(1);
                transform: scale(1);
        margin-left: 1vw;
    }

    section .list--area .anime--listview .list--itens .item--info .anime--info .title--info{
        font-size: 6.5vw;
    }

    section .list--area .anime--listview .list--itens .item--info .anime--info .genres--info,
    section .list--area .anime--listview .list--itens .item--info .anime--info .launch--info{
        font-size: 4vw;
        font-weight: normal;
    }


}
footer{
height: 150px;
background-color: #C1D4D9;
opacity: 0.3;
display: flex;
justify-content: center;
font-family: 'Poppins', sans-serif; 
color: #143259;  
}

.footer-info{
    padding: 15px 0;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    opacity: 1;
    font-weight: bold;
}

.social-media{
    font-size: 30px;
    display: flex;
    flex-direction: column;
}
.anime-page-background{
    width: 100vw;
    height: 100vh;
    background-color: rgba(20, 50, 89, 0.5);
    display: flex;
    position: fixed;
    justify-content: center;
    left: 0;
    top: 0;
    z-index: 4;   

}

.anime-page-background .anime-page-info{
    width: 45vw;
    height: 95vh;
    background-color: rgb(20, 50, 89);
    display: block;
    margin-top: 30px;
    position: relative;
    overflow-y: hidden;
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    border-radius: 25px;
    transition: all ease 0.2s;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5), 8px 8px 10px 5px rgba(0, 0, 0, 0.56);
}

.anime-page-background .anime-page-info .selected-anime-episode-info{
    color: #C1D4D9;
    margin-top: -6px;
    font-size: 17px;
    display: flex;
    flex-direction: column;
    background: linear-gradient(to bottom ,#000 , 35%, transparent 65%);
    border-bottom: 1px solid #666;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info{
    display: flex;
    padding-top: 30px;
    flex-direction: row;
    justify-content: end;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info .selected-anime-title{
    margin-right: 30px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info{
    display: flex;
    flex-direction: row;
    margin: 15px 30px;
    padding-bottom: 15px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .episode-title{
    width: 25vw;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons{
    margin: 0 30px;
    position: absolute;
    right: 0;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons .selected-season{
    width: 130px;
    height: 40px;
    background-color: #143259;
    border: 1px solid #666 ;
    color: #C1D4D9;
    text-align: center;
    border-radius: 10px;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info .ratio-info{
    color:#46d369;
    font-size: 18px;
    font-weight: bold;
    margin-right: 30px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .selected-episode-info .launch-info{
    font-weight: bold;
    margin-right: 30px;
}

.anime-page-background .anime-page-info .selected-anime-episode-info .episode-title{
    margin-left: 30px;
    font-weight: bold;
    font-size: 1.2rem;
}
.anime-page-background .anime-page-info .selected-anime-episode-info .episode-number{
font-size: 20px;
font-weight: bold;
}

.anime-page-info video{
    border-radius: 25px 25px 0px 0;
}
.anime-page-info .close-button{
    width: 30px;
    height: 30px;
    display:flex;
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 1;
    border-radius: 30px;
    align-items: center;
    justify-content: center;
    margin: 30px 30px;
    background-color: #143259;
}

.anime-page-info .close-button .close-icon{
    font-size: 25px;
    color: #C1D4D9;
}

.anime-page-background .anime-page-info .episodes--list{
    height: 30vh;
    color: #e2f1f5;
    display: flex;
    overflow-y: scroll;
    font-size: 15px;
    scroll-behavior: smooth;
    flex-direction: column;
}

.anime-page-background .anime-page-info .episodes--list .episode div{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 10px 0;
    margin-left: 15px;
    transition: all ease 0.2s;
}

.anime-page-background .anime-page-info .episodes--list .episode:hover{
    font-size: 18px;
    cursor: pointer;

}

.anime-page-background .anime-page-info .episodes--list .episode div .episode-number{
    margin-right: 30px;
    font-weight: bold;
}

.anime-page-background .anime-page-info .episodes--list .episode div .episode-title{
    width: 30vw;
}

.anime-page-background .anime-page-info .episodes--list .episode div .episode-thumb{
    width: 150px;
    height: 90px;
    margin-right: 15px;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar{
    width: 12px;
    -webkit-transition: all ease 0.2s;
    transition: all ease 0.2s;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar-track{
    background: rgba(193, 212, 217, 0.7);
    border-radius: 12px;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar-thumb{
    background-color: #143259;
    border-radius: 20px;
    border: 3px solid rgba(193, 212, 217, 0.7);
    opacity: 1;
}

.anime-page-background .anime-page-info .episodes--list::-webkit-scrollbar-thumb:hover{
    background-color: #143259;
    opacity: 0.5;
}

@media only screen and (max-width: 600px) {
    .anime-page-background{
        display: block;
        position:fixed;
       scroll-behavior: unset;
    }
    .anime-page-background .anime-page-info{
        width: inherit;
        height: inherit;
        margin-top: 0;
        border-radius: 0;
    }

    .anime-page-background .anime-page-info .close-button{
        width: 20px;
        height: 20px;
        margin: 15px 15px;
    }

    .anime-page-info video{
        border-radius: 0;
    }
    .anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .episode-title{
        width: 34vw;
        font-weight: normal;
        font-size: 5vw;
    }

    .anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons{
        margin-left: 0;
    }

    .anime-page-background .anime-page-info .selected-anime-episode-info .episode-title-info .selected-anime-seasons .selected-season{
        font-size: 4vw;
    }

    .anime-page-background .anime-page-info .episodes--list{
        height: 38vh;
    }
}

*{
    margin: 0px;
    padding: 0px;
    vertical-align: baseline;
    list-style: none;
    border: 0;
}

body{
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #143259;
}

body::-webkit-scrollbar{
    width: 0.5vw;
    -webkit-transition: all ease 0.2s;
    transition: all ease 0.2s;
}

body::-webkit-scrollbar-track{
    background: rgba(193, 212, 217, 0.1);
    border-radius: 12px;
}

body::-webkit-scrollbar-thumb{
    background-color: #C1D4D9;
    border-radius: 20px;
    border: 1px solid rgba(193, 212, 217, 0.7);
    opacity: 1;
}


